import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Stack,
} from '@mui/material';

import { BasicButton } from '../BasicButton';
import { useForm, Controller } from 'react-hook-form';
import type { SubmitHandler, DefaultValues } from 'react-hook-form';
import { useGetDealersQuery } from '../../utils/api';

export type FormValues = {
  dealer_id: string;
  date: string;
  matched: string;
};

const filtersDate = [
  { value: '', label: ' ' },
  { value: 'day', label: '' },
  { value: 'week', label: '' },
  { value: 'month', label: '' },
];

const filtersStatus = [
  { value: '', label: ' ' },
  { value: 'true', label: ' ' },
  { value: 'false', label: ' ' },
];

export const defaultValues: DefaultValues<FormValues> = {
  dealer_id: '',
  date: '',
  matched: '',
};

export default function FilterList({
  handleFiltersClick,
  handleFiltersReset,
}: {
  handleFiltersClick: (data: FormValues) => void;
  handleFiltersReset: () => void;
}) {
  const { handleSubmit, reset, control } = useForm<FormValues>({
    defaultValues,
  });

  const { data: dealersFilters } = useGetDealersQuery();

  const onSubmit: SubmitHandler<FormValues> = (filterData) => {
    handleFiltersClick(filterData);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Stack
        border={1}
        borderColor="action.disabledBackground"
        justifyContent="center"
        alignItems="center"
        alignContent="center"
        margin={5}
        padding={3}
        gap={5}
        direction="row"
        flexWrap="wrap"
        position="sticky"
        borderRadius={1}
      >
        <FormControl sx={{ minWidth: 120 }}>
          <InputLabel></InputLabel>
          <Controller
            render={({ field }) => (
              <Select {...field} label="">
                <MenuItem value=""> </MenuItem>
                {dealersFilters?.results.map((item: any) => {
                  return (
                    <MenuItem key={item.pk} value={item.pk}>
                      {item.name}
                    </MenuItem>
                  );
                })}
              </Select>
            )}
            name="dealer_id"
            control={control}
          />
        </FormControl>
        <FormControl sx={{ minWidth: 120 }}>
          <InputLabel></InputLabel>
          <Controller
            render={({ field }) => (
              <Select {...field} label="">
                {filtersStatus.map((item) => {
                  return (
                    <MenuItem key={item.label} value={item.value}>
                      {item.label}
                    </MenuItem>
                  );
                })}
              </Select>
            )}
            name="matched"
            control={control}
          />
        </FormControl>
        <FormControl sx={{ minWidth: 120 }}>
          <InputLabel></InputLabel>
          <Controller
            render={({ field }) => (
              <Select {...field} label="">
                {filtersDate.map((item) => {
                  return (
                    <MenuItem key={item.value} value={item.value}>
                      {item.label}
                    </MenuItem>
                  );
                })}
              </Select>
            )}
            name="date"
            control={control}
          />
        </FormControl>

        <BasicButton text="" type="submit" />
        <BasicButton
          text=""
          type="reset"
          onClick={() => {
            reset(defaultValues);
            handleFiltersReset();
          }}
        />
      </Stack>
    </form>
  );
}
